body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  overflow: hidden;
  max-height: 100vh;
}

body {
  background-color: #282c34;
  color: white;
  overflow: hidden;
  max-height: 100vh;
}

h1 {
  margin: 0;
}

.base {
  position: fixed;
  background: grey;
  border-top-right-radius: 15px;
}

.base:after, .q1:after {
  content: '';
  position: absolute;
  top: 10px;
  right: 10px;
  background: black;
  border-radius: 50%;
  width: 10px;
  height: 10px;
}

.base:before {
  content: '';
  position: absolute;
  right: 100%;
  width: 10000px;
  background: grey;
  top: 20px;
  height: 10px;
  opacity: 0.3;
}

.q1 {
  background: red;
}

.q2 {
  background: blue;
}

.q1, .q2 {
  position: fixed;
  opacity: 0.7;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.gripper {
  opacity: 0.7;
  position: absolute;
  height: 45px;
  width: 8px;
  background: grey;
  right: 0;
}

.gripper:after, .gripper:before {
  content: '';
  background: darkgrey;
  height: 8px;
  width: 30px;
  position: absolute;
}

.gripper:after {
  top: 0;
}

.gripper:before {
  bottom: 0;
}

.cursor {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: yellow;
  opacity: 0.4;
  position: fixed;
}

.controls {
  background: rgba(255, 255, 255, 0.2);
  margin: 0;
  padding: 20px;
  width: 710px;
}

